import { createRouter, createWebHistory } from 'vue-router';
import login from '../components/login1.vue';
import index from '../components/index.vue';

const routes = [
  {
    path: '/',
    name: 'login',
    component: login,
  },
  {
    path: '/index',
    name: 'index',
    component: index,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;