<script>
import axios from 'axios';
import * as echarts from 'echarts';
export default {
  props: {
    msg: String,
    tableData: Array,
  },
  data() {
    return {
      tableData: [],
      // radio: '1',
      // QDM: '',
      // PH: '',
      // Dw: '',
      // Dn: '',
      // L: '',
      // QDP: '',
      // QDT: '',
      // ST: '',
      // CH4: 0,
      // C2H6: 0,
      // C2H4: 0,
      // C3H8: 0,
      // C3H6: 0,
      // iC4H10: 0,
      // nC4H10: 0,
      // iC5H12: 0,
      // nC5H12: 0,
      // C6H14: 0,
      // C7H16: 0,
      // C8H18: 0,
      // N2: 0,
      // CO2: 0,
      // H2S: 0,
      // H2O: 0,
      // H2: 0,
      CH4: 0.9144,
      C2H6: 0.0168,
      C2H4: 0,
      C3H8: 0.0029,
      C3H6: 0,
      iC4H10: 0.0006,
      nC4H10: 0.0007,
      iC5H12: 0,
      nC5H12: 0.0012,
      C6H14: 0,
      C7H16: 0,
      C8H18: 0,
      N2: 0.0134,
      CO2: 0,
      H2S: 0,
      H2O: 0,
      H2: 0.05,
      QDM: 925925.92592,
      PH: 1.5,
      Dw: 1.130,
      Dn: 1.016,
      L: 43000,
      QDP: 8079000,
      QDT: 3,
      ST: 20,
      radio: '1',
      chartData: false,
      selectedoption: '曲线图',
      options: ['曲线图', '数据'],
      chartInstance: null, // 添加 chartInstance 属性
    };
  },

  methods: {
    calculate() {
      const dataToSend = {
        cH4: this.CH4,
        c2H6: this.C2H6,
        c2H4: this.C2H4,
        c3H8: this.C3H8,
        c3H6: this.C3H6,
        iC4H10: this.iC4H10,
        nC4H10: this.nC4H10,
        iC5H12: this.iC5H12,
        nC5H12: this.nC5H12,
        c6H14: this.C6H14,
        c7H16: this.C7H16,
        c8H18: this.C8H18,
        n2: this.N2,
        cO2: this.CO2,
        h2S: this.H2S,
        h2O: this.H2O,
        h2: this.H2,
        startM: this.QDM,
        ph: this.PH,
        dw: this.Dw,
        dn: this.Dn,
        length: this.L,
        startP: this.QDP,
        startT: this.QDT,
        solidtemp: this.ST,
        radio: this.radio,
      };
      // 计算组分总和
      const totalComposition = this.CH4 + this.C2H6 + this.C2H4 + this.C3H8 + this.C3H6 + this.iC4H10 + this.nC4H10 + this.iC5H12 + this.nC5H12 + this.C6H14 + this.C7H16 + this.C8H18 + this.N2 + this.H2 + this.CO2 + +this.H2S + this.H2O;
      // 如果组分总和不等于1，则弹出提示框
      if (totalComposition !== 1) {
        this.$message.error('组分信息错误，请确保所有组分的总和等于1');
        return; // 终止方法执行
      }
      axios.post('http://localhost:8088/execute-cpp', dataToSend, {
        headers: {
          'Content-Type': 'application/json',
        },
      })
        .then((response) => {
          console.log('executeCppProgram succeeded');
          // 执行完 C++ 程序后，发送请求获取计算结果
          if (this.radio == '1') {
            axios.get('http://localhost:8088//get-ret-txt-content')
              //axios.get('http://localhost:8088//receiveResult')
              .then((response) => {
                const results = response.data;
                // 将结果处理成表格数据
                const rows = results.trim().split('\n').map((row) => row.split(' '));
                this.tableData = rows.map((row) => ({
                  length: row[0],
                  pressure: row[1],
                  temperature: row[2],
                }));
                // 处理数据并更新 tableData
                this.processData();
                // 绘制图表
                this.drawChart();
              })
              .catch((error) => {
                console.error('Error getting result:', error);
              });
          }
          else {
            axios.get('http://localhost:8088//get-trans-txt-content')
              //axios.get('http://localhost:8088//receiveResult')
              .then((response) => {
                const results = response.data;
                // 将结果处理成表格数据
                const rows = results.trim().split('\n').map((row) => row.split(' '));
                this.tableData = rows.map((row) => ({
                  length: row[0],
                  pressure: row[1],
                  temperature: row[2],
                }));
                // 处理数据并更新 tableData
                this.processData();
                // 绘制图表
                this.drawChart();
              })
              .catch((error) => {
                console.error('Error getting result:', error);
              });
          }

        })
        .catch((error) => {
          console.error('Error executing C++ program:', error);
        });
    },
    processData() {
      const rows = [];
      for (const key in this.tableData) {
        rows.push({
          length: this.tableData[key].length,
          pressure: this.tableData[key].pressure,
          temperature: this.tableData[key].temperature
        });
      }
      this.tableData = rows;
    },
    drawChart() {
      // 提取数据并转换为数字
      const lengthData = this.tableData.map(item => parseFloat(item.length));
      const pressureData = this.tableData.map(item => parseFloat(item.pressure));
      const temperatureData = this.tableData.map(item => parseFloat(item.temperature));
      // 配置图表选项
      const options = {
        title: {
          text: '仿真结果曲线图',
          textStyle: {
            color: '#333', // 标题颜色
            fontSize: 18 // 标题字体大小
          }
        },
        xAxis: {
          type: 'category',
          data: lengthData,
          name: this.radio === '1' ? 'x(m)' : (this.radio === '2' ? 't(s)' : ''), // 根据this.radio的值设置x轴名称
          nameLocation: 'center',
          nameTextStyle: {
            verticalAlign: "top",
            lineHeight: 30
          }
        },
        yAxis: [ // 使用 yAxis 数组定义多个纵坐标轴
          {
            type: 'value',
            name: 'P(bar)', // 设置左侧 y 轴名称
            position: 'left', // 设置左侧 y 轴位置
            // 根据数据的范围动态设置坐标轴的最小值和最大值
            min: (Math.min(...pressureData) - (Math.max(...pressureData) - Math.min(...pressureData)) / 10).toFixed(1),
            max: (Math.max(...pressureData) + (Math.max(...pressureData) - Math.min(...pressureData)) / 10).toFixed(1)
          },
          {
            type: 'value',
            name: this.radio === '1' ? 'T(℃)' : (this.radio === '2' ? 'Qm(kg/s)' : ''), // 设置右侧 y 轴名称
            position: 'right' // 设置右侧 y 轴位置
          }
        ],
        legend: { // 添加 legend 选项
          data: ['Pressure', this.radio === '1' ? 'Temperature' : (this.radio === '2' ? 'Mass flow rate' : '')] // 设置图例数据
        },
        series: [
          {
            name: 'Pressure',
            type: 'line',
            data: pressureData,
            yAxisIndex: 0 // 将 Pressure 系列绑定到左侧 y 轴
          },
          {
            name: this.radio === '1' ? 'Temperature' : (this.radio === '2' ? 'Mass flow rate' : ''),
            type: 'line',
            data: temperatureData,
            yAxisIndex: 1 // 将 Temperature 系列绑定到右侧 y 轴
          }
        ]

      };
      const chart = echarts.init(document.getElementById('chart'));
      chart.setOption(options);
    },
  },
};
</script>

